@import "../../common.scss";

.select {
    height: $searchSize;
    border-radius: $border-radius;
    min-width: 128px;
    text-align: center;
    font-size: 16px;
    padding: 0 16px;
    background-color: $base-color;
    border: 1px solid $color;
}

.option {
    height: $searchSize;
    border-radius: $border-radius;
    border: 1px solid $color;
}